.app__header {
    background-color: var(--color-black);
}

.app__wrapper_info {
    margin-top: 5rem;
}

.app__header-h1 {
    font-family: var(--font-base);
    color: var(--color-golden);
    letter-spacing: 0.04em;
    text-transform: uppercase;
    line-height: 117px;
    font-size: 70px;
}

.app__header-img img {
    width: 80%;
}

@media screen and (max-width:900px) {
    
    .app__header-h1 {
        font-size: 60px;
        line-height: 70px;
    }

    .app__wrapper_info {
        margin-top: 5rem;
    }

}

@media screen and (max-width:650px) {
    .app__header-h1 {
        font-size: 40px;
        line-height: 70px;
    }
}